import ccui from '../index';
// import { TinyEmitter } from 'tiny-emitter';
// export const emitter = new TinyEmitter();

export enum MenuType {
  Item = 'item',
  Separator = 'separator',
}
export interface IUiMenuItem {
  name?: string;
  /**
   * 
   */
  tip?: string;
  /**
   * 
   */
  type?: MenuType;
  /**
   * 
   */
  enabled?: boolean;
  /**
   * 
   */
  visible?: boolean;
  /**
   * falseiconicon
   */
  selected?: boolean;
  /**
   * icon
   */
  icon?: string;
  /**
   * 
   */
  shortKey?: string;
  callback?: (item: IUiMenuItem, event: MouseEvent) => void | null;
  /**
   * 
   */
  enter?: ((item: IUiMenuItem) => void) | null;
  /**
   * 
   */
  leave?: ((item: IUiMenuItem) => void) | null;
}

export const Msg = {
  ShowMenu: 'show-menu',
  HideMenu: 'hide-menu',
};

export interface MenuOptions {
  x: number;
  y: number;
  /**
   * [0-1]
   */
  opacity: number;
}

export function showMenuByMouseEvent(event: MouseEvent, newMenus: IUiMenuItem[], opacity: number = 1): void {
  const options: MenuOptions = {
    x: event.clientX + 2,
    y: Math.abs(event.clientY),
    opacity,
  };
  ccui.Emitter.emit(Msg.ShowMenu, options, newMenus || []);
}
